video{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container{
    // background: url("../../assets/images/img-home.jpg") center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
    color: white;

    h1{
        font-size: 6vw;
    }
}


.play-container{
    background-color: white;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    text-align: left;
   
    i{
        font-size: 40px;
        color:black;
        margin-left: -20px;
    }
}

.hero-btns{
    display: flex;
    column-gap: 10px;
    row-gap: 10px;
}